let Style ={
    canvasColor:'#FFFFFF',
    Candle:{
        up:{
            fillStyle:'#f85c5c',
            brushType:'both',
            strokeStyle:"#f85c5c"
        },
        down:{
            strokeStyle:'#50c88c',
            brushType:'stroke'
        }
    },
    upColor:'#f85c5c',
    downColor:'#50c88c',
    lineColor:'#666',
    gridColor:'#666',
    tickLabelColor:'#a9a9a9',
    crossColor:'#999999',
    tipColor:'#f1f1f1'
}

export default Style


